<script>
  module.exports = {
    data () {
      return {
        value3: '',
        value4: '',
        value5: '',
        datetimes: {
          date0: '',
          date1: '',
          date2: '',
          daterange0: '',
          daterange1: '',
          daterange2: '',
          datetime1: '',
          datetimerange1: '',
          week: '',
          month: '',
          year: '',
          timeSelect: '',
          time0: '',
          startTime: '',
          endTime: '',
          timeRange: [],
        },
        pickerOptions: {
          date1: {
            disabledDate(time) {
              return time.getTime() < Date.now() - 8.64e7
            }
          },
          date2: {
            shortcuts: [{
              text: '',
              onClick(picker) {
                picker.$emit('pick', new Date())
              }
            }, {
              text: '',
              onClick(picker) {
                const date = new Date()
                date.setTime(date.getTime() - 3600 * 1000 * 24)
                picker.$emit('pick', date)
              }
            }, {
              text: '',
              onClick(picker) {
                const date = new Date()
                date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
                picker.$emit('pick', date)
              }
            }]
          },
          daterange1: {
            disabledDate(time) {
              let correctTime = time.getTime() + 8.64e7
              let startTime = Date.now()
              let endTime = Date.now() + 30 * 24 * 60 * 60 * 1000
              return correctTime < startTime || correctTime > endTime
            }
          },
          daterange2: {
            shortcuts: [{
              text: '',
              onClick(picker) {
